<template>
  <div>
    <my-header></my-header>
    <main class="main">
      <div class="content-modify" v-if="userType === 0">
        <div class="title">我的信息</div>
        <div class="content-item">
          <div class="item-title">会员号:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.memberId" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">会员姓名:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.memberName" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">性别:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.gender" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">城市:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.city" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">地址:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.address" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">邮编:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.mailNumber" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">电话:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.phoneNumber" class="input-box">
          </div>
        </div>
        <div class="content-item">
          <div class="item-title">电子邮件:</div>
          <div class="item-content">
            <input type="text" v-model="myInfo.Email" class="input-box">
          </div>
        </div>
        <div class="title">我的订单</div>
        <div v-for="(order,index) in orderList" :key="index" class="order">
          <div class="order-item">
            <div class="item-left">
              <img :src="order.productImg" class="item-left-img">
              <div class="item-left-text">
                <p>{{order.productName}}</p>
                <p>{{order.productType}}</p>
              </div>
            </div>
            <div class="item-right">
              <div>订单号：{{order.orderID}}</div>
              <div>单价：￥ {{order.productPrice}}</div>
              <div>数量：{{order.quantity}}</div>
              <div>小结：￥{{order.sum}}</div>
              <div>状态：{{order.status}}</div>
            </div>
          </div>
        </div>
        <div class="btn-view">
          <div class="save-btn">保存</div>
        </div>
      </div>
      <div class="content-modify" v-if="userType === 1">
        <div class="title">订单管理</div>
        <div v-for="(order,index) in orderList" :key="index" class="order">
          <div class="order-item">
            <div class="item-left">
              <img :src="order.productImg" class="item-left-img">
              <div class="item-left-text">
                <p>{{order.productName}}</p>
                <p>{{order.productType}}</p>
              </div>
            </div>
            <div class="item-right">
              <div>订单号：{{order.orderID}}</div>
              <div>单价：￥ {{order.productPrice}}</div>
              <div>数量：{{order.quantity}}</div>
              <div>小结：￥{{order.sum}}</div>
              <div>状态：{{order.status}}</div>
            </div>
            <div class="item-btn-view">
              <div>发货地址： <br> {{order.address}}</div>
              <div class="btn-ship" v-if="order.status === '未发货'">发货</div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import myHeader from '@/components/home/Header'
export default {
  name: 'MyInfo',
  components: {
    myHeader
  },
  data () {
    return {
      userType: 1,
      myInfo: {
        memberId: '20180501001',
        memberName: '张筱',
        gender: '男',
        city: '武汉',
        address: '武汉大学信息学部C4 726舍',
        mailNumber: '430070',
        phoneNumber: '18216190526',
        Email: '975825364@qq.com'
      },
      orderList: [{
        orderID: '2019050102',
        productName: '1989',
        productImg: require('@/assets/1989.jpg'),
        productType: '录音室专辑',
        quantity: 2,
        productPrice: 125,
        sum: 250,
        status: '未发货',
        address: '武汉大学信息学部C4 726舍'
      }]
    }
  }
}
</script>

<style scoped>
  .main{
    background-color: #f3f4f6;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .content-modify{
    width: 800px;
    margin: 0 auto;
    border: 1px solid white;
    background: white;
    border-radius: 10px;
  }
  .title{
    width: 200px;
    margin-left: 22px;
    border-bottom: 1px solid #00a1d6;
    margin-top: 50px;
    color: #00a1d6;
    font-size: 16px;
    margin-bottom: 25px;
    padding-bottom: 10px;
  }
  .content-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 22px;
    margin-left: 50px;
  }
  .item-title{
    width: 80px;
    font-size: 14px;
    text-align: right;
    margin-right: 20px;
    color: #48576a;
  }
  .input-box{
    width: 300px;
    height: 30px;
    border-radius: 4px;
    border: 1px solid #bfcbd9;
    box-sizing: border-box;
    color: #1f2d3d;
    padding: 3px 10px
  }
  .order-item{
    display: flex;
    flex-direction: row;
    padding-left: 25px;
    border: 1px solid #9F92A7;
    margin: 22px;
    position: relative;
  }
  .order-item:hover{
    border-color: #00a1d6;
  }
  .item-left{
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    color: #48576a;
  }
  .item-right{
    margin-left: 70px;
  }
  .item-btn-view{
    margin-left: 30px;
  }
  .item-left-img{
    width: 100px;
    height: 100px;
  }
  .item-left-text{
    margin-left: 10px;
  }
  .btn-view{
    width: 700px;
    border-top: 1px solid #00a1d6;
    margin: 0 auto;
    background: white;
  }
  .save-btn{
    width: 110px;
    color: #fff;;
    background: #00a1d6!important;
    border-color: #20a0ff;
    border-radius: 4px;
    padding: 10px 10px;
    cursor: pointer;
    font-size: 14px;
    text-align: center;
    margin: 30px auto;
  }
  .save-btn:hover{
    background: #20a0ff;
    border-color: #20a0ff;
  }
  .save-btn:active{
    background: #1d90e6;
    border-color: #1d90e6;
  }
  .btn-ship{
    width: 110px;
    color: #fff;;
    background: #00a1d6!important;
    border-color: #20a0ff;
    border-radius: 4px;
    padding: 10px 10px;
    cursor: pointer;
    font-size: 14px;
    text-align: center;
    position: absolute;
    right: 50px;
    bottom: 10px;
  }
</style>



