<template>
  <div>
    <my-header></my-header>
    <main>
      <section class="flex-row content">
        <div>
          <img :src="resource.imgUrl" class="content-img"/>
        </div>
        <div class="content-text">
          <p class="title">{{resource.productName}}</p>
          <div class="content-text-item">
            <p class="inline-text">价格：</p>
            <p class="content-price inline-text">￥{{resource.productPrice}}</p>
          </div>
          <div class="content-text-item">
            <p class="inline-text">产地：</p>
            <p class="inline-text">{{resource.productWhere}}</p>
          </div>
          <div class="content-text-item">
            <p class="inline-text">类型：</p>
            <p class="inline-text">{{resource.productType}}</p>
          </div>
          <div class="content-text-item">
            <p class="inline-text">制作人：</p>
            <p class="inline-text">{{resource.director}}</p>
          </div>
          <div class="content-text-item">
            <p class="inline-text">歌手：</p>
            <p class="inline-text">{{resource.singer}}</p>
          </div>
          <div class="flex-row">
            <button class="btn" @click="purchase">立即购买</button>
            <button class="btn" style="margin-left: 100px" @click="addToBucketList">加入购物车</button>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import myHeader from '@/components/home/Header'
import axios from 'axios'
export default {
  name: 'detail',
  components: {
    myHeader
  },
  data () {
    return {
      resource: {
        imgUrl: require('@/assets/1989.jpg'),
        productType: '录音室专辑',
        productName: '1989',
        productPrice: '125',
        productWhere: '美国',
        director: 'Max Martin',
        singer: 'Taylor Swift'
      }
    }
  },
  methods: {
    getResourceSucc (res) {
      var data = res.data
      this.resource = data.detailInfo
    },
    purchase () {
      this.$router.push({path: '/cart'})
    },
    goToBucketList () {}
  },
  created () {
    axios().then(this.getResourceSucc)
  }

}
</script>

<style scoped>
  .title{
    font-size: 30px;
    font-weight: 700;
    line-height: 35px;
    color: #3C3C3C;
    margin-bottom: 30px;
  }
  .flex-row{
    display: flex;
    flex-direction: row;
  }
  .content{
    margin-left: 200px;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .content-img{
    width: 400px;
    height: 400px;
  }
  .content-text{
    margin-left: 100px;
  }
  .content-text-item{
    margin-bottom: 30px;
  }
  .content-price{
    color: #f40;
    font-weight: 700;
    font-size: 35px;
    margin-left: 10px;
  }
  .inline-text{
    display: inline;
    vertical-align: center;
    width: 64px;
  }
  .btn{
    margin: 30px auto 0;
    color: white;
    background: rgb(46, 130, 255);
    border: 0;
    border-radius: 3px;
    display: block;
    height: 40px;
    line-height: 40px;
    width: 100px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    appearance: none;
  }
</style>



